const UPDATE_SCORE = 'UPDATE_SCORE'
const RESET_SCORE = 'RESET_SCORE'
const LEVEL_UP = 'LEVEL_UP'
const SET_PLAYER = 'SET_PLAYER'
const SCORE_BOARD = 'SCORE_BOARD'

export function setPlayer(name){
  return{
    type:SET_PLAYER,
    payload:name
  }
}


export function updateScore(score){
  return{
    type:UPDATE_SCORE,
    payload:score
  }
}

export function resetScore(){
  return{
    type:RESET_SCORE,
  }
}

export function levelUp(condition){
  return{
    type:LEVEL_UP,
    payload:condition
  }
}

export function scoreBoard(condition){
  return{
    type:SCORE_BOARD,
    payload:condition
  }
}

