import React,{useState,useEffect} from 'react'
import { useDispatch } from 'react-redux'
import {scoreBoard} from './redux/actions'
import {useSelector} from 'react-redux'
import axios from 'axios'

export default function Scoreboard() {
    const [score,setScore] = useState([])
    const dispatch = useDispatch()
    const score_board = useSelector(state => state.scoreBoard)
    const info = async() => {
      let database = await axios.get(' https://snake-react-game.herokuapp.com').then(({data}) =>dispatch(scoreBoard(data)))
      return database
    }
    useEffect(()=>{
        info()
    },[])
    
    return (
        <div className="scoreboard">
            {score_board.length > 0 && <h1>Scoreboard</h1>}
            <ol>
            {
             score_board.map(score => <li>{score.score} by {score.username}</li>  )
            }
            </ol>
        </div>
    )
}
